import * as React from 'react';
import { Routes, Route, useLocation, Navigate } from 'react-router-dom';

import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { ReactQueryDevtools } from '@tanstack/react-query-devtools';

import Login from './login/Login';
import { useAuth } from './hooks/auth';
import { AuthProvider } from './providers/auth';
import Layout from './layout/layout';
import StartPage from './components/Start';
import Loader from './components/Loader';
const NotFoundPage = React.lazy(() => import('./components/NotFoundPage'));
const TrucksNearby = React.lazy(() => import('./trucksNearby/trucksNearby'));
const Trucks = React.lazy(() => import('./trucks/trucks'));
const Loads = React.lazy(() => import('./loads/loads'));
const Owners = React.lazy(() => import('./owners/owners'));
const Coordinators = React.lazy(() => import('./coordinators/coordinators'));
const Drivers = React.lazy(() => import('./drivers/drivers'));
const Locations = React.lazy(() => import('./locations/locations'));
const Users = React.lazy(() => import('./users/users'));
const GeneralMap = React.lazy(() => import('./GoogleMaps/map'));

const queryClient = new QueryClient();

export default function App() {
  return (
    <AuthProvider>
      <QueryClientProvider client={queryClient}>
        <Routes>
          <Route path="/login" element={<Login />} />
          <Route
            path="*"
            element={
              <RequireAuth>
                <Layout>
                  <React.Suspense fallback={<Loader visible />}>
                    <Routes>
                      <Route path="/trucks_nearby" element={<TrucksNearby />} />
                      <Route path="/general_map" element={<GeneralMap />} />
                      <Route path="/loads" element={<Loads />} />
                      <Route path="/trucks" element={<Trucks />} />
                      <Route path="/owners" element={<Owners />} />
                      <Route path="/coordinators" element={<Coordinators />} />
                      <Route path="/drivers" element={<Drivers />} />
                      <Route path="/locations" element={<Locations />} />
                      <Route path="/users" element={<Users />} />
                      <Route path="/" element={<StartPage />} />
                      <Route path="*" element={<NotFoundPage />} />
                    </Routes>
                  </React.Suspense>
                </Layout>
              </RequireAuth>
            }
          />
        </Routes>
        <ReactQueryDevtools initialIsOpen={false} />
      </QueryClientProvider>
    </AuthProvider>
  );
}

function RequireAuth({ children }: { children: JSX.Element }) {
  const auth = useAuth();
  const location = useLocation();

  if (!auth.user) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }
  return children;
}
